
.pad-content {
  text-align: left;
  padding: 1em;
}
.word {
  display: inline-block;
  margin-right: 0.3em;
  user-select: none;
}
.highlight-block {
  display: inline-block;
  position: absolute;
  height: 1.4em;
  background-color: rgba(255, 255, 0, 0.6);
  border-radius: 2px;
  z-index: -99;
}



