
.popup {
  position: absolute;
  border: 1px solid gray;
  box-shadow: 2px 2px 5px gray;
  text-align: left;
  padding: 1em;
  box-sizing: content-box;
  background-color: white;
  border-radius: 3px;
  min-width: 10em;
}
.popup .description {
  border-top: 1px solid gray;
  padding-top: 0.3em;
}
.title {
  display: block;
  display: flex;
  justify-content: space-between;
}



